import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { AuthenticationService } from 'src/app/core/services/authentication/authentication.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  hasError: boolean;

  isShow = false;

  returnUrl: string;
  isSubmitted: boolean;
  success: boolean;
  loading: boolean;
  errorMessage: string;

  constructor(
    private fb: FormBuilder,
    private authService: AuthenticationService,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.initForm();
    // get return url from route parameters or default to '/'
    // this.returnUrl = this.route.snapshot.queryParams['returnUrl'.toString()];
    // this.route.queryParams.subscribe((params) => {
    //   this.returnUrl = params.returnUrl || '/';
    //   console.log('returnUrl', this.returnUrl);
    // });
  }

  // convenience getter for easy access to form fields
  get f() {
    return this.loginForm.controls;
  }

  initForm() {
    this.loginForm = this.fb.group({
      email: [
        '',
        Validators.compose([
          Validators.required,
          Validators.email,
          Validators.minLength(3),
          Validators.maxLength(320),
        ]),
      ],
      password: [
        '',
        Validators.compose([
          Validators.required,
          Validators.minLength(3),
          Validators.maxLength(100),
        ]),
      ],
    });
  }

  onSubmit() {
    this.isSubmitted = true;
    this.success = false;
    this.hasError = false;

    if (this.f.invalid) {
      return;
    }

    const obj = {
      strEmail: this.loginForm.value.email,
      strPassword: this.loginForm.value.password,
    };

    this.authService.adminLogin(obj).subscribe((res) => {
      this.loading = false;
      if (res.success) {
        this.success = true;

        localStorage.setItem('userId', res.data.fkAdminId);
        localStorage.setItem('userName', res.data.strName);
        localStorage.setItem('email', this.loginForm.value.email);
        localStorage.setItem(
          'role',
          res.data.strUserType === 1
            ? 'a165fccd-2bbe-4391-9ea9-a237b1fb7c82'
            : 'e5e069e0-1914-4fc9-bd4a-90487fce89ea'
        );

        const modules = res.data.arryModuleDetails;
        localStorage.setItem('modules', JSON.stringify(modules));

        localStorage.setItem('token', res.data.token);

        setTimeout(() => {
          window.location.href = modules[0].path;
        }, 500);
      } else {
        this.hasError = true;
        this.errorMessage = res.message;
      }
    });
  }

  // ngOnDestroy() {
  //   this.unsubscribe.forEach((sb) => sb.unsubscribe());
  // }
}
