import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { UserService } from 'src/app/core/services/user/user.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss'],
})
export class ForgotPasswordComponent implements OnInit {
  forgotPasswordForm: FormGroup;
  resetPasswordForm: FormGroup;
  loading = false;
  errorMessage = '';
  step1 = true;

  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private router: Router,
    private toasterService: ToastrService
  ) {}

  ngOnInit(): void {
    this.initForm();
  }

  // convenience getter for easy access to form fields
  get f() {
    return this.forgotPasswordForm.controls;
  }
  get r() {
    return this.resetPasswordForm.controls;
  }

  initForm() {
    this.forgotPasswordForm = this.fb.group({
      email: [
        '',
        Validators.compose([
          Validators.required,
          Validators.email,
          Validators.minLength(3),
          Validators.maxLength(320),
        ]),
      ],
    });
    this.resetPasswordForm = this.fb.group({
      otp: [
        '',
        Validators.compose([
          Validators.required,
          Validators.minLength(5),
          Validators.maxLength(5),
        ]),
      ],
      newPassword: [
        '',
        Validators.compose([
          Validators.required,
          Validators.minLength(6),
          Validators.maxLength(20),
        ]),
      ],
      confirmPassword: [
        '',
        Validators.compose([
          Validators.required,
          Validators.minLength(6),
          Validators.maxLength(20),
        ]),
      ],
    });
  }

  submit() {
    if (this.forgotPasswordForm.invalid) {
      return;
    }

    const obj = {
      strEmail: this.f.email.value,
    };

    this.loading = true;
    this.userService.sentOTP(obj).subscribe((result: any) => {
      this.loading = false;
      if (result.success) {
        this.step1 = false;
      } else {
        this.step1 = true;
        this.errorMessage = result.message;
      }
    });
  }

  submitReset() {
    if (this.resetPasswordForm.invalid) {
      return;
    }

    if (this.r.newPassword.value !== this.r.confirmPassword.value) {
      this.errorMessage = 'Password and Confirm Password must be same';
      return;
    }

    const obj = {
      strEmail: this.f.email.value,
      strOTP: this.r.otp.value + '',
      strNewPaswd: this.r.newPassword.value,
    };

    this.loading = true;
    this.userService.VerifyOTPResetPassword(obj).subscribe((result: any) => {
      this.loading = false;
      if (result.success) {
        this.step1 = true;
        this.errorMessage = '';
        this.toasterService.success(result.message);
        this.router.navigate(['/auth/login']);
      } else {
        this.step1 = false;
        this.errorMessage = result.message;
      }
    });
  }
}
