import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { CommonService } from '../common/common.service';
import { ApiResponse } from '../../model/api-response.models';
import { catchError, retry } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class UserService {
  private apiURL: string = environment.API_ENDPOINT;

  constructor(private http: HttpClient, private commonService: CommonService) {}

  httpOptions = {
    headers: new HttpHeaders({
      Authorization: 'bearer ' + localStorage.getItem('token'),
    }),
  };

  getDashboard(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'dashbaord/get_dashboard',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  updatePassword(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'profile/update_password',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  sentOTP(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'otp/send_otp_for_reset',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  VerifyOTPResetPassword(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'otp/verify_otp_and_passwd_reset',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  getPushNotification(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'user/get_push_nottifications',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  pushStatusChange(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'user/push_status_change',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  clearAllPush(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'user/push_clear_all',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
}
